<template>
  <div>
    <div class="alert-container" v-if="alertActive">
      <vs-alert :active.sync="alertActive" :color="alertVariant" closable icon-pack="feather" close-icon="icon-x" class="mb-5">
        <p>{{ alertMessage }}</p>
      </vs-alert>
    </div>

    <nv-card>
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 required">
          <label class="vs-input--label">{{ $t('APP') }}</label>
          <vs-select autocomplete class="w-full" v-model="postingRule.appId">
            <vs-select-item v-for="(app, index) in apps" :key="index" :value="app.appId" :text="app.appName"/>
          </vs-select>
        </div>
        <div class="nv-col sm:w-1/2 w-full required">
          <label class="vs-input--label">{{ $t('TRANSFER_TYPE') }}</label>
          <funds-transfer-type-lookup :title="$t('TRANSFER_TYPE')" :recordValue="postingRule.transferTypeId" :recordDisplay="postingRule.transferTypeName" valueField="typeId" displayField="typeName" @select-record="selectTransferTypeRecord"/>
        </div>
      </div>
      <div class="nv-row mb-4 timepicker-wrap">
        <div class="nv-col sm:w-1/2 w-full required">
          <label class="vs-input--label">{{ $t('FROM_TIME') }}</label>
          <div class="vs-inputx vs-input--input bg-white w-full">
            <vue-timepicker v-model="postingRule.fromTime" format="HH:mm:ss"></vue-timepicker>
          </div>
        </div>
        <div class="nv-col sm:w-1/2 w-full required">
          <label class="vs-input--label">{{ $t('TO_TIME') }}</label>
          <div class="vs-inputx vs-input--input bg-white w-full">
            <vue-timepicker v-model="postingRule.toTime" format="HH:mm:ss"></vue-timepicker>
          </div>
        </div>
      </div>
      <div class="nv-row mb-4 timepicker-wrap">
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('CURRENCY_TYPE') }}</label>
          <div class="flex list-style-none p-2">
            <li class="left mr-4" v-for="(enumeration, index) in currencyTypes" :key="index">
              <vs-radio v-model="postingRule.currencyTypeEnumId" :vs-value="enumeration.enumId">{{ enumeration.description }}</vs-radio>
            </li>
          </div>
        </div>
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('AMOUNT') }}</label>
          <div class="flex">
            <vs-input-number v-model="postingRule.fromAmount" min="0"/>
            <span class="mt-2 mx-4"> - </span>
            <vs-input-number v-model="postingRule.toAmount" min="0"/>
          </div>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('ROLLOVER') }}</label>
          <div class="flex">
            <vs-input-number v-model="postingRule.rollover" min="0"/>
          </div>
        </div>
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('POSITION') }}</label>
          <div class="flex">
            <vs-input-number v-model="postingRule.position" min="0"/>
          </div>
        </div>
      </div>
      <div class="nv-row mb-4" v-if="postingRule.currencyTypeEnumId === 'FT_CCY_EXPLICIT'">
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('DEBIT_CURRENCY') }}</label>
          <currency-lookup :title="$t('DEBIT_CURRENCY')" :recordValue="postingRule.debitCurrencyId" :recordDisplay="postingRule.debitCurrencyDescription" valueField="uomId" displayField="description" @select-record="selectDebitCurrencyRecord"/>
        </div>
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('TRANSFER_CURRENCY') }}</label>
          <currency-lookup :title="$t('TRANSFER_CURRENCY')" :recordValue="postingRule.transferCurrencyId" :recordDisplay="postingRule.transferCurrencyDescription" valueField="uomId" displayField="description" @select-record="selectTransferCurrencyRecord"/>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('PARTY_CLASS') }}</label>
          <party-class-lookup :title="$t('PARTY_CLASS')" :recordValue="postingRule.partyClassificationId" :recordDisplay="postingRule.partyClassificationDescription" valueField="partyClassificationId" displayField="description" @select-record="selectPartyClassRecord"/>
        </div>
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('EXTERNAL_PARTY_ID') }}</label>
          <vs-input class="w-full" v-model="postingRule.externalPartyId"/>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('FROM_DATE') }}</label>
          <nv-input-group>
            <template slot="append">
              <div class="append-text bg-dark">
                <i class="far fa-calendar-alt"></i>
              </div>
            </template>
            <datepicker class="w-full" :placeholder="$t('SELECT_DATE')" v-model="postingRule.fromDate" :format="dateFormat"></datepicker>
          </nv-input-group>
        </div>
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('TO_DATE') }}</label>
          <nv-input-group>
            <template slot="append">
              <div class="append-text bg-dark">
                <i class="far fa-calendar-alt"></i>
              </div>
            </template>
            <datepicker class="w-full" :placeholder="$t('SELECT_DATE')" v-model="postingRule.toDate" :format="dateFormat"></datepicker>
          </nv-input-group>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('POSTING_TYPE') }}</label>
          <vs-select autocomplete class="w-full" :placeholder="$t('SELECT_VALUE')" v-model="postingRule.postingTypeEnumId">
            <vs-select-item v-for="(enumeration, index) in postingTypes" :key="index" :value="enumeration.enumId" :text="enumeration.description"/>
          </vs-select>
        </div>
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('DESCRIPTION') }}</label>
          <vs-input class="w-full" v-model="postingRule.description"/>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col w-full">
          <vs-checkbox v-model="postingRule.disabled">{{ $t('DISABLED') }}</vs-checkbox>
        </div>
      </div>
      <div class="nv-row">
        <div class="nv-col w-full">
          <div class="flex justify-between">
            <vs-button class="px-5 py-3" color="primary" type="border" @click="cancel">{{ $t('CANCEL') }}</vs-button>
            <div class="dropdown-button-container">
              <vs-button class="btnx" type="filled" @click="modifyPostingRule('browse')">{{ $t('SUBMIT') }}</vs-button>
              <vs-dropdown vs-trigger-click>
                <vs-button class="btn-drop" type="filled" icon="fa-angle-down" icon-pack="far"></vs-button>
                <vs-dropdown-menu>
                  <vs-dropdown-item @click="modifyPostingRule('stay')">
                    <span>{{ $t('SUBMIT_STAY') }}</span>
                  </vs-dropdown-item>
                  <vs-dropdown-item @click="modifyPostingRule('view')">
                    <span>{{ $t('SUBMIT_VIEW') }}</span>
                  </vs-dropdown-item>
                </vs-dropdown-menu>
              </vs-dropdown>
            </div>
          </div>
        </div>
      </div>
    </nv-card>
  </div>
</template>

<script>
import AppService from '@/services/commons/AppService'
import EnumerationService from '@/services/commons/EnumerationService'
import PostingRuleService from '@/services/ft/PostingRuleService'
import AlertMixin from '@/mixins/commons/AlertMixin'
import DatePickerMixin from '@/mixins/commons/DatePickerMixin'
import TimePickerMixin from '@/mixins/commons/TimePickerMixin'
import FundsTransferTypeLookup from '@/views/ft/funds-transfer-type/FundsTransferTypeLookup'
import CurrencyLookup from '@/views/nucleus/currency/CurrencyLookup'
import PartyClassLookup from '@/views/nucleus/party-class/PartyClassLookup'
import VueTimepicker from 'vue2-timepicker'
import Datepicker from 'vuejs-datepicker'

export default {
  name: 'posting-rule-modify',
  mixins: [AlertMixin, DatePickerMixin, TimePickerMixin],
  components: {
    FundsTransferTypeLookup,
    CurrencyLookup,
    PartyClassLookup,
    VueTimepicker,
    Datepicker
  },
  data: function () {
    return {
      postingRule: {},
      apps: [],
      currencyTypes: [],
      postingTypes: []
    }
  },
  methods: {
    async load () {
      let self = this
      PostingRuleService.postingRules({
        ruleId: this.$route.params.id
      }).then(function (response) {
        self.postingRule = response.data.postingRuleList[0] || {}
        self.postingRule.fromTime = self.timeFromString(self.postingRule.fromTime)
        self.postingRule.toTime = self.timeFromString(self.postingRule.toTime)
        self.postingRule.disabled = self.postingRule.disabled === 'Y'
      })

      AppService.apps({
      }).then(function (response) {
        self.apps = response.data.appList || []
      })

      EnumerationService.enumerations({
        enumTypeId: 'FundsTransferCurrencyType',
        orderByField: 'sequenceNum,description'
      }).then(function (response) {
        self.currencyTypes = response.data.enumerationList || []
      })

      EnumerationService.enumerations({
        enumTypeId: 'FundsTransferPostingType',
        orderByField: 'sequenceNum,description'
      }).then(function (response) {
        self.postingTypes = response.data.enumerationList || []
      })
    },
    selectTransferTypeRecord (record) {
      this.postingRule.transferTypeId = record.typeId || ''
    },
    selectTransferCurrencyRecord (record) {
      this.postingRule.transferCurrencyId = record.uomId || ''
    },
    selectDebitCurrencyRecord (record) {
      this.postingRule.debitCurrencyId = record.uomId || ''
    },
    selectPartyClassRecord (record) {
      this.postingRule.partyClassificationId = record.partyClassificationId || ''
    },
    modifyPostingRule (nextAction) {
      let self = this
      PostingRuleService.modifyPostingRule({
        ruleId: this.$route.params.id,
        appId: this.postingRule.appId,
        transferTypeId: this.postingRule.transferTypeId,
        currencyTypeEnumId: this.postingRule.currencyTypeEnumId,
        transferCurrencyId: this.postingRule.transferCurrencyId,
        debitCurrencyId: this.postingRule.debitCurrencyId,
        fromTime: this.timeToString(this.postingRule.fromTime),
        toTime: this.timeToString(this.postingRule.toTime),
        partyClassificationId: this.postingRule.partyClassificationId,
        externalPartyId: this.postingRule.externalPartyId,
        fromAmount: this.postingRule.fromAmount,
        toAmount: this.postingRule.toAmount,
        fromDate: this.getTime(this.postingRule.fromDate),
        toDate: this.getTime(this.postingRule.toDate),
        postingTypeEnumId: this.postingRule.postingTypeEnumId,
        description: this.postingRule.description,
        rollover: this.postingRule.rollover,
        position: this.postingRule.position,
        disabled: this.postingRule.disabled ? 'Y' : 'N'
      }).then(function (response) {
        if (response.status === 200) {
          if (nextAction === 'stay') {
            self.showSuccessAlert(response.data.messages)
          } else if (nextAction === 'view') {
            self.$router.push({ name: 'ViewPostingRule', params: { id: response.data.purposeId } })
          } else {
            self.$router.push({ name: 'BrowsePostingRules' })
          }
        }
      }).catch(function (error) {
        self.showDangerAlert(error.response.data.errors)
      })
    },
    cancel () {
      this.$router.back()
    }
  },
  mounted: function () {
    this.load()
  }
}
</script>
