<template id="popup">
  <vs-popup fullscreen :title="title" :active.sync="activeInternal">
    <div :ref="popupRef" class="vs-con-loading__container">
      <vs-table class="mb-base" :data="partyClassTableData" :noDataText="partyClassTableNoDataText" :sst="true" search @search="filterPartyClassTable" @sort="orderPartyClassTable" @selected="selectRow">
        <div slot="header" class="flex flex-wrap-reverse items-center flex-grow justify-between">
          <div class="flex flex-wrap-reverse items-center">
            <vs-button class="p-3 mr-4" @click="loadPartyClassTableData(popupRef)" type="filled" icon="fa-sync" icon-pack="far" :disabled="partyClassTableBusy">{{ $t('REFRESH') }}</vs-button>
          </div>

          <div class="flex justify-end mr-4">
            <div class="p-2 border border-solid border-grey-light rounded-full d-theme-dark-bg flex items-center justify-between font-medium">
              <span class="mx-2">{{ partyClassTablePagingInfo }}</span>
              <vs-dropdown vs-trigger-click class="cursor-pointer mr-2">
                <span class="far fa-angle-down text-primary"></span>
                <vs-dropdown-menu>
                  <vs-dropdown-item :key="index" v-for="(item, index) in tablePageOptions" @click="partyClassTablePerPage = item">
                    <span>{{ item }}</span>
                  </vs-dropdown-item>
                </vs-dropdown-menu>
              </vs-dropdown>
            </div>
          </div>
        </div>

        <template slot="thead">
          <vs-th sort-key="partyClassificationId">
            {{ this.$t('ID') }}
          </vs-th>
          <vs-th sort-key="description">
            {{ this.$t('CLASS_NAME') }}
          </vs-th>
          <vs-th sort-key="classificationTypeDescription">
            {{ this.$t('CLASS_TYPE') }}
          </vs-th>
        </template>

        <template slot-scope="{data}">
          <vs-tr :key="index" v-for="(tr, index) in data" :data="tr">
            <vs-td :data="data[index].partyClassificationId">
              {{ data[index].partyClassificationId }}
            </vs-td>
            <vs-td :data="data[index].description">
              {{ data[index].description }}
            </vs-td>
            <vs-td :data="data[index].classificationTypeDescription">
              {{ data[index].classificationTypeDescription }}
            </vs-td>
          </vs-tr>
        </template>
      </vs-table>

      <vs-pagination :total="partyClassTableTotalPages" v-model="partyClassTableCurrentPage" :max="maxPaginationItems"></vs-pagination>
    </div>
  </vs-popup>
</template>

<script>
import PopupMixin from '@/mixins/commons/PopupMixin'
import PartyClassTableMixin from '@/mixins/nucleus/PartyClassTableMixin'

export default {
  name: 'party-class-popup',
  mixins: [PopupMixin, PartyClassTableMixin],
  mounted: function () {
    this.loadPartyClassTableData(this.popupRef)
  },
  beforeDestroy: function () {
    this.$el.remove()
  }
}
</script>
